button {
  font-family: $font-text;
}

@mixin gl_btn() {
  border-radius: $border-radius;
  padding: $padding 40px;
  border: 1px solid;
  font-size: $font-size;
  cursor: pointer;
  font-family: $font-text;
  font-weight: $font-weight;
}

@mixin remove-animation() {
  transition: none;
  transform: none;
}

@mixin box-shadow-initial() {
  box-shadow: 0 2px 6px 0 rgba(253, 122, 105, 0.3);
}
