$whiteColor: #FFF;
$ctaColor: $whiteColor;
$blackColor: #000000;
$blackLightColor: #333333;
$bittersweet: #fd7a69;
$trueGreen: #04ac00;
$blueberryColor: #5b7dff;
$blueberryLightColor : #74b9ff;
$tomatoColor: #ff5858;
$redColor: #d63031;
$redLightColor: #ff7675;
$orangeColor: #ad6815;
$orangeLightColor: #fa983a;
$greenColor: #00b894;
$greenLightColor: #55efc4;

//Gray shading
$lightGray2: #e2e2e2;
$lightGray3: #d8d8d8;
$pinkish-gray: #ccc;
$grayDarker: #b0b0b0;
$silver: #c6c6c6;
$gray35: #595959;
$Gray40: #666666;
$Gray79: #c9c9c9;
$grayDF: #dfe6e9;
$gray44: #444444;
$grayF6: #f6f6f6;

//Fonts
$font-gimli: 'Gimli';
$font-info: 'Playfair Display', serif;
$font-text: 'Nunito Sans', sans-serif;
$font-weight: bold;
$font-weight-xtra: 800;
$font-light: 300;
$font-thin: 200;
$font-size: 16px;
$font-size-xs: 12px;
$font-size-xl: 24px;

//
$border-radius: 3px;

// border box
$border-radius-box: 8px;

// Size
$baseSize: 8px;

$size-xs: $baseSize / 2; // 4px
$size-sm: $baseSize; // 8px
$size-md: $baseSize * 2; // 16px
$size-lg: $baseSize * 3;  // 24px
$size-xl: $baseSize * 4;  // 32px
$size-xxl: $baseSize * 5;  // 40px

$fontSize: $size-md;

// margin
$margin-xs: $size-xs;
$margin-sm: $size-sm;
$margin: $size-md;
$margin-md: $size-md;
$margin-lg: $size-lg;
$margin-xl: $size-xl;
$margin-xxl: $size-xxl;

$margin-bottom: $margin-lg;

$checkbox-size: $size-md;
$margin-small: $margin-sm;

// padding
$padding-xs: $size-xs;
$padding-sm: $size-sm;
$padding: $size-md;
$padding-md: $size-md;
$padding-lg: $size-lg;
$padding-xl: $size-xl;
$padding-xxl: $size-xxl;

// Tooltip colors
$tooltip-bg-color : rgba(0, 0, 0, 0.75);
$tooltip-shadow-color : rgba(0, 0, 0, 0.25);
