.gl_slider {
  input[type='range'] {
    --range: calc(var(--max) - var(--min));
    --ratio: calc((var(--val) - var(--min))/var(--range));
    --sx: calc(.5*1.5em + var(--ratio)*(100% - 1.5em));
    margin: 0;
    padding: 0;
    width: 12.5em;
    height: 1.5em;
    background: transparent;
    font: 1em/1 arial, sans-serif;
  }
  input[type='range'], [type='range']::-webkit-slider-thumb {
    -webkit-appearance: none;
  }
  input[type='range']::-webkit-slider-runnable-track {
    box-sizing: border-box;
    border: none;
    width: 12.5em;
    height: 0.25em;
    background: $pinkish-gray;
  }
  input[type='range']::-webkit-slider-runnable-track {
    background:linear-gradient(to right, $blueberryColor , $blueberryColor) 0/var(--sx) 100% no-repeat #ccc;
  }
  input[type='range']::-moz-range-track {
    box-sizing: border-box;
    border: none;
    width: 12.5em;
    height: 0.25em;
    background: $pinkish-gray;
  }
  input[type='range']::-ms-track {
    box-sizing: border-box;
    border: none;
    width: 12.5em;
    height: 0.25em;
    background: $pinkish-gray;
  }
  input[type='range']::-moz-range-progress {
    height: 0.25em;
    background: $blueberryColor;
  }
  input[type='range']::-ms-fill-lower {
    height: 0.25em;
    background: $blueberryColor;
  }
  input[type='range']::-webkit-slider-thumb {
    margin-top: -0.625em;
    box-sizing: border-box;
    border: none;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    background: $blueberryColor;
  }
  input[type='range']::-moz-range-thumb {
    box-sizing: border-box;
    border: none;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    background: $blueberryColor;
  }
  input[type='range']::-ms-thumb {
    margin-top: 0;
    box-sizing: border-box;
    border: none;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    background: $blueberryColor;
  }
  input[type='range']::-ms-tooltip {
    display: none;
  }
}
