@mixin gl_mixin_boxArrowRight(){
  width: 0;
  height: 0;
  border-top: 45px solid transparent;
  border-bottom: 45px solid transparent;
  border-left: 60px solid;
}
.gl_box {
  &Utility {
    display: flex;
    padding: 0;
    margin: $margin-lg;

    &_container {
      display: flex;
      padding: $padding-xxl;
    }
    &_icon {
      width: 48px;
      height: auto;
      display: flex;
      align-items: center;
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;
      color: $whiteColor;
      background-color: $Gray79;

      input[type=radio] {
        display: none;
      }
      .box-arrow-right {
        @include gl_mixin_boxArrowRight;
        border-left-color: $Gray79;
      }
      .gl-i-settings {
        position: absolute;
        left: $size-md;
      }
    }
    &_content {
      padding: $padding-md;
      display: flex;
      justify-content: center;
      flex-direction: column;
    }
    &_title {
      font-size: $font-size-xl;
      font-weight: $font-weight;
      padding: $padding-sm $padding-md;
    }
    &_detail {
      display: flex;
      align-items: center;
      padding: 0 $padding-md;
      font-size: $font-size-xs;
    }
    &.active {
      border-color: $blueberryColor;
      .gl_boxUtility_icon {
        background-color: $blueberryColor;
        .box-arrow-right {
          border-left-color: $blueberryColor;
        }
      }
      .gl_boxUtility_title {
        color: $blueberryColor;
      }
    }
  }
}

@media (max-width: 991px) {
  .gl_boxUtility{
    &_container {
      flex-direction: column;
    }
  }
}
