.gl_list {
  position: relative;
  select {
    display:block;
    width: 100%;
    height: 45px;
    padding: 10px 16px;
    margin: $margin 0;
    border: 1px solid $grayDarker;
    border-radius: 3px;
    font-family: $font-text;
    color: $grayDarker;
    font-size: $size-md;
    background-color: $whiteColor;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    transition: 0.5s transform ease;
    &::-ms-expand {
      display: none;
    }
    &:focus {
      animation: gl_anim_borderHighlighter 0.4s ease;
      animation-fill-mode: forwards;
    }
  }
  &::after {
    content: '\e917';
    font-family: $font-gimli;
    font-size: $size-md;
    font-weight: bold;
    position: absolute;
    top: 15px;
    right: 16px;
    color: $pinkish-gray;
    pointer-events: none;
    transform: rotate(90deg);
  }
  &:focus {
    &::after {
      transform: rotate(180deg);
    }
  }
}
