.gl_receipt {
    &_item {
      padding: 16px 0;
      border-top: solid 1px $Gray79;
      &:first-child {
        border: none;
      }
  
      &Container {
        display: flex;
        align-items: flex-start;
      }
      &Icon {
        padding-right: 16px;
        max-width: 35px;
        i {
          font-size: 18px;
        }
      }
      &Content {
        display: flex;
        flex-direction: column;
        width: 100%;
        font-size: 14px;
        line-height: 1.3;
        color: $Gray79;
      }
      &Price {
        white-space: nowrap;
        display: flex;
        flex-direction: column;
        line-height: 1.1;
        font-size: 14px;
        align-items: flex-end;
        align-self: flex-start;
        &-option {
          align-self: flex-end;
        }
        &_old {
          text-decoration: line-through;
          color: $Gray79;
        }
        &_discount {
          color: $blueberryColor;
        }
        &_text {
          font-size: 12px;
        }
        &-total {
          font-size: 24px;
          .gl_receipt_itemPrice_real {
              font-weight: bold;
          }
        }
      }
      &_title {
        color: $blackColor;
      }
      &Option {
        display: flex;
        flex-direction: column;
        &_item {
          margin-top: 4px;
          width: 100%;
        }
        &_itemContainer {
          display: flex;
          width: 100%;
          font-size: 14px;
          align-items: flex-end;
          &::before {
            content: '';
            width: 42px;
            height: 1px;
          }
        }
        &_remove {
          margin-right: 16px;
          display: none;
        }
        &_title {
          width: 100%;
            i {
                width: 18px;
                text-align: left;
                margin: 0;
            }
        }
        & & {
          .gl_receipt_itemOption_itemContainer::before {
            width: 66px;
          }
        }
        & + & {
          > .gl_receipt_itemOption_item {
            margin-top: 12px;
          }
        }
      }
  
      &-taxe {
        font-size: 12px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      }
      &-total {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      &Total {
        &_title {
          font-size: 16px;
          font-weight: bold;
        }
      }
    }
  }
