.gl_pills {
  font-family:"Nunito Sans",sans-serif;
  @include gl_pills();

  span {
    @include centerAbs();
  }

  &_label {
    padding: 0 8px;
    max-width: 100%;
    font-size: 14px;
    overflow-wrap: break-word;
  }

  &_icon {
    font-size: 24px;
    &.gl-i-flag {
      margin-left: 4px;
    }
  }

  /* Colors */
  &-blue {
    @include infoBg();
    &:hover {
      box-shadow: 0 2px 12px 0 rgba($blueberryColor, 0.5);
    }
  }

  &-red {
    @include criticalBg();
    &:hover {
      box-shadow: 0 2px 12px 0 rgba($redLightColor, 0.5);
    }
  }

  &-green {
    @include sucessBg();
    &:hover {
      box-shadow: 0 2px 12px 0 rgba($greenColor, 0.5);
    }
  }

  &-orange {
    @include warningBg();
    &:hover {
      box-shadow: 0 2px 12px 0 rgba($orangeLightColor, 0.5);
    }
  }

  &-gray {
    @include disableBg();
    color: $gray44;
    &:hover {
      box-shadow: 0 2px 12px 0 rgba($grayDF, 0.5);
    }
  }

  /* State */
  &:active {
    @include remove-animation();
    @include box-shadow-initial();
  }

  &.disabled {
    box-shadow: none;
    @include remove-animation();
  }
}
