/*----- Custom Tippy Tooltip -----*/
.tippy-tooltip.gl-theme {
  background-color: $tooltip-bg-color;
  color: $whiteColor;
  padding: 11px 30px;
  border-radius: 4px;
  box-shadow: 0 2px 5px 0 $tooltip-shadow-color;
  min-height: 40px;

  &[data-placement^='top'] .tippy-arrow {
    border-top-color: $tooltip-bg-color;
  }
  &[data-placement^='bottom'] .tippy-arrow {
    border-bottom-color: $tooltip-bg-color;
  }
  &[data-placement^='right'] .tippy-arrow {
    border-right-color: $tooltip-bg-color;
  }
  &[data-placement^='left'] .tippy-arrow {
    border-left-color: $tooltip-bg-color;
  }
}