.gl_profil {
  position: relative;
  cursor: pointer;
  font-family:"Nunito Sans",sans-serif;

  &:after, &_picture:before {
    content: '';
    opacity: 0;
  }

  &:after {
    position: absolute;
    top: -5px;
    left: -5px;
    width: calc(100% + 10px);
    height: calc(100% + 10px);
    border: 1px dashed $grayDarker;
    border-radius: 100%;
    transform: rotate(0deg);
    transition: opacity 200ms, transform 600ms;
  }

  &_picture {
    @include gl_profil();
    &:before {
      @include centerAbs();
      width: 100%;
      height: 100%;
      background-color: rgba($whiteColor, 0.35);
      transition: opacity 200ms;
    }
  }

  &-edit {
    opacity: 1;
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: 24px;
    color: $grayDarker;
    transition: opacity 200ms;
  }

  &:hover {
    &:after {
      opacity: 1;
      transform: rotate(45deg);
    }

    .gl_profil {
      &_picture:before {
          opacity: 1;
      }
      &-edit {
        opacity: 0;
      }
    }
  }
}
