.gl_box {
  display: flex;
  position: relative;
  border: solid 1px $Gray79;
  background-color: $whiteColor;
  margin: 22px 0;
  padding: 25px;
  cursor: pointer;
  @include gl_mixin_boxBorder($border-radius-box);
  @include gl_mixin_secondBorder($border-radius-box, $blueberryColor);
  @include gl_mixin_boxcheck($blueberryColor, $whiteColor);
  &.disabled {
    color: $grayDarker;
    cursor: not-allowed;
  }

  &_input {
    display: flex;
    align-items: center;
  }
  &Icon {
    display: flex;
    align-self: center;
    padding: $padding-lg 48px;

    .gl_icon {
      color: $Gray79;
      width: 72px;
      font-size: 72px;
      display: flex;
      justify-content: center;
    }
  }
  &Content {
    display: flex;
    justify-content: center;
    margin: 8px 24px;
    flex-direction: column;
  }
  &Title {
    font-weight: $font-weight;
    font-size: 18px;
  }
  &Title,
  &Describe {
    margin: $margin-sm;
  }

  &.gl_radioBtn:not(.disabled) {
    input[type="radio"] {
      &:before {
        border-color: $blueberryColor;
      }
    }
  }
  &_toggle {
    flex-direction: column;
    &Befor {
      display: flex;
    }
    .gl_boxDescribe,
    .gl_boxDescribe_toggle,
    &After {
      overflow: hidden;
      transition: all 400ms ease;
    }
    .gl_boxDescribe {
      opacity: 1;
    }
    .gl_boxDescribe_toggle {
      opacity: 0;
    }
    &.animPulse {
      cursor: auto;

      .gl_boxDescribe_toggle ~ .gl_boxDescribe {
        opacity: 0;
      }
      .gl_boxDescribe_toggle {
        opacity: 1;
      }
    }
  }
}
.gl_box_toggleMobile {
  display: none;
}

.animPulse {
  &::before {
    opacity: 1;
    transition: opacity 300ms ease;
  }
  &::after {
    animation-name: gl_anim_pulse;
    animation-duration: 300ms;
    animation-timing-function: ease;
    opacity: 1;
    transform: scale(.75);
  }
}

@media  (max-width: 991px) {
  .gl_box {
    &Icon {
      padding: $padding-lg 24px;
      .gl_icon {
        width: 48px;
        font-size: 48px;
      }
    }
  }
}

@media  (max-width: 767px) {
  .gl_box {
    &.gl_radioBtn {
      padding: 16px;
      .gl_radio input {
        margin: 0;
      }
      .gl_boxContent {
        margin: 8px 0 8px 8px;
      }
      .gl_boxTitle,
      .gl_boxDescribe {
        margin: 8px 0;
      }
      .gl_boxDescribe {
        margin-left: -40px;
      }
    }
    &Icon {
      padding: 16px 8px 16px 16px;
      align-self: flex-start;
      .gl_icon {
        width: 24px;
        font-size: 24px;
      }
    }
  }
  .gl_box_toggleMobile {
    position: fixed;
    top: 0;
    height: 100%;
    width: 100%;
    left: 100%;
    background-color: #fff;
    transition: left 300ms ease;
    z-index: 9999;
    padding: 42px 16px 28px;
    overflow: auto;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.3);

    &.show {
      display: block;
    }
    &.open {
      left: 0;
      display: block;
    }
    &_close {
      font-size: 32px;
      padding: 16px 24px;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
    }
    .gl_box_toggleBefor {
      align-items: stretch;
    }
    .gl_box_input,
    .gl_boxDescribe_toggle ~ .gl_boxDescribe {
      display: none;
    }
    .gl_boxIcon {
      padding: 16px;
      align-self: center;
    }
    .gl_boxDescribe,
    .gl_boxDescribe_toggle,
    .gl_box_toggleAfter {
      max-height: none !important;
    }
  }
}
