// -- vars
$height: 20 / 16 * 1em;
$height-text: 30 / 16 * 1em;
$width: 50 / 16 * 1em;
$puceRadius: 14 / 16 * 1em;
$puceRadius-text: 24 / 16 * 1em;

$color-white: #fefefe;
$color-clouds: #ecf0f1;
$color-peter-river: #3498db;

$transition-default: 0.25s ease-out 0.10s;


.gl_switch {
  margin-bottom: $margin-bottom;
  height: $height;
  position: relative;
  width: $width;

  input:focus ~ .switch {
    @include focusContent();
  }

  .switch {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    border-radius: 20 / 16 * 1em;
    background: $grayDarker;
    height: $height;
    left: 4 / 16 * -1em;
    top: 3 / 16 * -1em;
    transition: background-color $transition-default;
    top: 0;
    left: 0;

    &Puce {
      position: absolute;
      top: 3px;
      left: 3px;
      border-radius: 50%;
      background: $color-white;
      height: $puceRadius;
      width: $puceRadius;
      transform: translate(0, 0);
      transition: left $transition-default;
    }
  }

  .switch-input {
    position: absolute;
    z-index: 999;
    width: 100%;
    height: 100%;
    opacity: 0;

    &:checked {
      & ~ .switch {
        background: $trueGreen;
        .switchPuce {
          left: calc(100% - #{$puceRadius + 3 / 16});
        }
      }
    }
  }
  &-text {
    height: $height-text;

    .lbl-off,
    .lbl-on {
      cursor: pointer;
      display: block;
      font-size: 0.9em;
      font-weight: bold;
      line-height: 1.3em;
      position: absolute;
      top: 8 / 16 * 1em;
      transition: opacity $transition-default;
      text-transform: uppercase;
      text-align: center;
    }
    &.load {
      .lbl-off,
      .lbl-on {
        width: calc(100% - #{$puceRadius + 24 / 16});
      }
    }
  
    .lbl-off {
      right: 7 / 16 * 1em;
    }
  
    .lbl-on {
      color: $color-white;
      opacity: 0;
      left: 7 / 16 * 1em;
    }
    .switch {
      position: absolute;
      height: $height-text;
    }

    .switch-input {
      &:checked {
        & ~ .lbl-off {
          opacity: 0;
        }
  
        & ~ .lbl-on {
          opacity: 1;
        }

        & ~ .switch {
          .switchPuce {
            left: calc(100% - #{$puceRadius-text + 3 / 16});
          }
        }
      }
    }
  
    .switch {
      &Puce {
        height: $puceRadius-text;
        width: $puceRadius-text;
      }
    }
  }
}
