@import url('https://fonts.googleapis.com/css?family=Nunito+Sans:200,300,300i,400,400i,600,600i,700,700i,800|Playfair+Display&display=swap');
@import url('https://static.eliberty.fr/gimli-font/v1.15.0/style.css');

.gl_font {
  &-nunitoSans {
    font-family: $font-text;
  }

  &-playfair {
    font-family: $font-info;
  }

  /* Heading */
  &_heading {
    font-weight: $font-weight-xtra;
  }

  &-primary {
    font-size: 48px;
    letter-spacing: -1.5px;
  }

  &-secondary {
    font-size: $size-xl;
    letter-spacing: -1px;
  }

  &-tertiary {
    font-size: $size-lg;
    letter-spacing: -0.75px;
  }

  &-fourth {
    font-size: $size-md;
    letter-spacing: -0.5px;
  }

  /* Hero */
  &-hero {
    font-size: 60px;
  }

  &-subhero {
    font-size: 30px;
  }

  /* Label */
  &-label {
    font-family: $font-info;
    font-size: 14px;
  }
}

.gl {
  /* Weight */
  &-bold {
    font-weight: $font-weight;
  }

  &-xtra {
    font-weight: $font-weight-xtra;
  }

  &-light {
    font-weight: $font-light;
  }

  &-thin {
    font-weight: $font-thin;
  }

  /* Decoration */
  &-underlined {
    text-decoration: underline;
  }
}
