@mixin round() {
  display: flex;
  height: 34px;
  width: 34px;
  border: 1px solid;
  border-radius: 100%;
  justify-content: center;
  margin-bottom: $margin;
  background-color: $whiteColor;
}

@mixin breadcrumb-items() {
  text-decoration: none;
  font-weight: $font-weight;
  font-size: 12px;
  letter-spacing: -0.38px;
  display: flex;
  align-items: center;
  align-self: flex-start;
  position: relative;
}

@mixin overlay($time: 300ms) {
  display: none;
  background-color: #000000;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  cursor: pointer;
  &.opening {
    display: block;
    opacity: 0;
    transition: opacity $time ease;
  }
  &.open {
    display: block;
    opacity: 0.25;
  }
  &.closing {
    opacity: 0;
    transition: opacity $time ease;
  }
}
