.gl_card {
  position: relative;
  width: 265px;
  height: 320px;
  border-radius: 9px;
  box-shadow: 0 2px 12px 0 rgba($blackColor, 0.25);
  overflow: hidden;
  font-family:"Nunito Sans",sans-serif;

  &:before {
    @include centerAbs();
    content: '';
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to bottom, rgba($blackColor, 0) 50%, rgba($blackColor, 0.5));
  }
}
