.gl_input {
  &_content {
    position: relative;
    width: 330px;
    margin: $margin 0;
    font-family: $font-text;
    font-size: 14px;
    display: table-caption;
  }
  &_input {
    display:block;
    width: 100%;
    height: 56px;
    padding: 10px 24px 10px 16px;
    border: 1px solid $grayDarker;
    border-radius: 3px;
    font-family: $font-text;
    color: $gray35;
    &:focus {
      animation: gl_anim_borderHighlighter 0.4s ease;
      animation-fill-mode: forwards;
    }
    &.error {
      border-color: $tomatoColor;
    }
    &_adddoc {
      border: 1px dashed $grayDarker;
      &:focus {
        animation: gl_anim_borderDashedHighlighter 0.4s ease;
        animation-fill-mode: forwards;
      }
    }
    &::placeholder {
      color: $grayDarker;
    }
  }
  &_label {
    &.control-label,
    input[value=""]:not(:focus) ~ &.control-label  {
      color: $gray35;
      font-size: 14px;
      position:absolute;
      pointer-events:none;
      top: 20px;
      padding-left: 16px;
      transition:0.2s ease all;
      -moz-transition:0.2s ease all;
      -webkit-transition:0.2s ease all;
      &.masked {
        color: transparent;
      }
    }
    /* active state = label top */
    input:focus ~ &.control-label,
    input:valid ~ &.control-label,
    input:not([value=""]):not(:focus) ~ &.control-label	{
      top: 5px;
      font-size: $size-sm;
      color: $grayDarker;
    }
  }

  &_message-error {
    color: $tomatoColor;
    font-size: 11px;
    padding: 0 0 0 8px;
    position: absolute;
    bottom: -15px;
  }

  &_message-hinted {
    color: $grayDarker;
    font-size: 11px;
    padding: 0 0 0 8px;
    position: absolute;
    bottom: -15px;
    &::before {
      content:"\e937";
      font-family: $font-gimli;
      padding-right: 5px;
    }
  }
}

input:required ~ .input-required:after {
  content: "*";
  font-size: 20px;
  position: absolute;
  top: 22px;
  right: 8px;
  color: $grayDarker;
}
