.gl_cart_summary {
  display: flex;
  justify-content: space-between;
  padding: 24px 0;
  border-bottom: solid 1px $pinkish-gray;

  &Text {
    font-size: 24px;
    &-price {
      font-weight: bold;
    }
  }
  &ButtonToggle {
    cursor: pointer;
    display: flex;
    align-items: center;
    &Text {
      font-size: 16px;
      font-weight: bold;
    }
    .gl-icon {
      margin-left: 16px;
      font-size: 28px;
    }
    &:hover &Text {
      text-decoration: underline;
    }
  }

  &_sidebar {
    display: none;
    position: fixed;
    right: 0;
    top: 0;
    width: 500px;
    background-color: #fff;
    border-left: solid 1px $pinkish-gray;
    border-top: solid 1px $pinkish-gray;
    bottom: 0;
    z-index: 5;
    padding: 62px;
    overflow: auto;
    &.opening {
      display: block;
      right: -500px;
      transition: right 300ms ease;
    }
    &.open {
      display: block;
      right: 0;
    }
    &.closing {
      right: -500px;
      transition: right 300ms ease;
    }

    .gl-i-close {
      position: absolute;
      top: 15px;
      left: 15px;
      font-size: 24px;
      cursor: pointer;
    }

    &_title {
      font-weight: bold;
    }
    @media (max-width: 600px) {
      width: 100%;
      padding: 62px 24px;

      &.opening{
        right: -100%;
      }
      &.open {
        right: 0;
      }
      &.closing {
        right: -100%;
      }
    }
  }
}

.gl_sidebarOverlay {
  @include overlay();

  @media (min-width: 1300px) {
    &.open {
      display: none;
    }
  }
}

@media (min-width: 1300px) {
  .gl_ContainerSidebarAnimation {
    margin-right: calc((100% - 1170px) / 2) !important;
    margin-left: calc((100% - 1170px) / 2) !important;
    &.opening {
      transition: margin 300ms ease, width 300ms ease;
    }
    &.open {
      width: 770px;
      margin-right: calc((100% - 1270px) / 2 + 500px) !important;
      margin-left: calc((100% - 1270px) / 2) !important;
    }
  }
}

@media (min-width: 1500px) {
  .gl_ContainerSidebarAnimation {
    &.open {
      width: 970px;
      margin-right: calc((100% - 1470px) / 2 + 500px) !important;
      margin-left: calc((100% - 1470px) / 2) !important;
    }
  }
}

@media (min-width: 1700px) {
  .gl_ContainerSidebarAnimation {
    &.open {
      width: 1170px;
      margin-right: calc((100% - 1670px) / 2 + 500px) !important;
      margin-left: calc((100% - 1670px) / 2) !important;
    }
  }
}

@media (min-width: 769px) {
  .gl_cart_summary {
    &_sidebar {
      top: 60px;
    }
  }
}

@media (max-width: 767px) {
  .gl_cart_summary {
    flex-direction: column;
  }
}
