@mixin justify-align-center() {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin focusContent() {
  outline: 2px dotted #c2c2c2;
}
@mixin focus() {
  &:focus {
    @include focusContent();
  }
}

@mixin centerAbs() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin centerAbsY() {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

@mixin centerAbsX() {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
}

@mixin infoBg() {
  background-color: $blueberryColor;
}
@mixin criticalBg() {
  background-color: $redLightColor;
}
@mixin sucessBg() {
  background-color: $greenColor;
}
@mixin warningBg() {
  background-color: $orangeLightColor;
}
@mixin disableBg() {
  background-color: $grayDF;
}

@keyframes gl_anim_borderHighlighter {
  from {
    border: 1px solid $grayDarker;
  }
  to {
    border: 1px solid $blueberryColor;
  }
}

@keyframes gl_anim_borderDashedHighlighter {
  from {
    border: 1px dashed $grayDarker;
  }
  to {
    border: 1px dashed $blueberryColor;
  }
}

// box
@keyframes gl_anim_pulse{
  0%{
    transform: scale(.25);
  }
  50%{
    transform: scale(1);
  }
  100%{
    transform: scale(.75);
  }
}
@mixin gl_mixin_boxBorder($borderRadiusBox){
  border-radius: $borderRadiusBox;
}

@mixin gl_mixin_secondBorder($borderRadiusBox, $borderColor){
  &::before {
    content: " ";
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: solid 1px $borderColor;
    opacity: 0;
    pointer-events: none;
    @if ($borderRadiusBox > 0px) {
      border-radius: $borderRadiusBox - 1px;
    } @else  {
      border-radius: $borderRadiusBox;
    }
  }
}

@mixin gl_mixin_boxcheck($backgroundColor, $textColor){
  &::after {
    display: flex;
    align-items: center;
    justify-content: center;
    content: "\e93b";
    font-family: $font-gimli;
    font-size: $size-sm;
    font-weight: bold;
    position: absolute;
    top: -10px;
    right: -10px;
    width: 25px;
    height: 25px;
    border-radius: 100%;
    background-color: $backgroundColor;
    z-index: 1;
    color: $textColor;
    line-height: 9px;
    opacity: 0;
    transition: opacity 300ms ease, transform 500ms ease;
    transform: scale(.25);
  }
}