.gl_alert {
  font-family:"Nunito Sans",sans-serif;
  @include gl_alert();

  &_label, &_icon, &_icon span{
    display: inline-block;
    vertical-align: middle;
  }

  &_label {
    padding: 16px 0 10px;
    font-size: $size-md;
    max-width: calc(100% - 50px);
    a {
      color: $whiteColor;
      text-decoration: underline;
    }
  }

  &_icon {
    padding: 12px 26px 12px 0;
    width: 24px;
    height: 24px;
    box-sizing: content-box;

    span {
      font-size: 24px;
    }
  }

  /* Value */
  &-info {
    @include infoBg();
  }

  &-critical {
    @include criticalBg();
  }

  &-success {
    @include sucessBg();
  }

  &-warning {
    @include warningBg();
  }

  /* State */
  &.disabled {
    @include disableBg();
    a {
      cursor: not-allowed;
      pointer-events: none;
    }
  }
}
