.gl_label {
  font-family:"Nunito Sans",sans-serif;
  @include gl_label();

  /* Value */
  &-info {
    @include infoBg();
    color: $blueberryLightColor;
  }

  &-critical {
    @include criticalBg();
    color: $redColor;
  }

  &-success {
    @include sucessBg();
    color: $greenLightColor;
  }

  &-warning {
    @include warningBg();
    color: $orangeColor;
  }

  /* State */
  &.disabled {
    @include disableBg();
    color: $whiteColor;
  }
}
