.gl_breadcrumb {
  margin: 32px 0;
  position: relative;
  font-family:"Nunito Sans",sans-serif;

  &_wrapper {
    &.return {
      position: absolute;
      top: 0;
      left: 0;
    }

    &.list {
      @include justify-align-center();
      counter-reset: flag;
      flex-direction: row;
      overflow: hidden;
      text-align: center;
    }
  }

  .round {
    font-size: 14px;
  }
}

.breadcrumb {
  &_return {
    &.inactive {
      color: $silver;
    }
    @include breadcrumb-items();
    flex-direction: row;
    cursor: pointer;
    &.done {
      color: $silver;
    }
    .round {
      @include round();
      align-items: center;
      justify-content: center;
      margin-bottom: 0;
      margin-right: 8px;
      border: 0;
      background-color: initial;
      border: 1px solid;
      &:before {
        content: '\e910';
        font-family: $font-gimli;
        display: inline-block;
        color: $silver;
        font-size: 10px;
        position: absolute;
        margin-right: 2px;
        margin-top: 2px;
      }
    }
  }

  &_list {
    @include breadcrumb-items();
    padding: 0 3%;
    flex-direction: column;
    &::before,
    &::after {
        content: '';
        position: absolute;
        z-index: -1;
        top: 18px;
        height: 1px;
        background-color: $Gray79;
    }
    &::before {
      left: 0;
      right: 50%;
    }
    &::after {
      left: 50%;
      right: 0;
    }
    &:first-child {
      &::before {
        display: none;
      }
    }
    &:last-child {
      &::after {
        display: none;
      }
    }

    .round {
      position: relative;
      @include round();
      &:before {
        content: counter(flag);
        counter-increment: flag;
        width: 20px;
        height: 20px;
        line-height: 20px;
        margin: 8px 0;
        position: absolute;
        top: 0;
        font-weight: $font-weight;
      }
    }
    &.active {
      color: initial;
    }
    &.inactive {
      color: $silver;
    }
    &.disabled {
      cursor: not-allowed;
      pointer-events: none;
    }
    &.done {
      color: $silver;
      cursor: pointer;
      .round {
        &:before {
          font-family: $font-gimli;
          content: '\e93b';
          display: inline-block;
          color: $silver;
          font-size: 8px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .gl_breadcrumb_wrapper {
    .breadcrumb_list {
      max-width: 150px;
    }
  }
}
@media screen and (max-width: 991px) and (min-width: 768px) {
  .gl_breadcrumb_wrapper {
    .breadcrumb_list {
      max-width: 150px;
    }
  }
}

@media screen and (min-width: 768px) {
  .breadcrumb_list_label {
    white-space: nowrap;
  }
}

@media screen and (max-width: 767px) {
  .gl_breadcrumb_wrapper {
    .breadcrumb_list {
      max-width: 90px;
    }
  }
}

@media screen and (max-width: 599px) {
  .gl_breadcrumb {
    margin: 0;
    padding: 16px 15px;
    background-color: $grayF6;

    .round {
      background-color: $grayF6;
    }

    &_wrapper {
      &.return {
        left: 15px;
        top: 16px;
      }
      &.list {
        margin-left: 42px;
        overflow-x: visible;
      }
    }
  }

  .breadcrumb {
    &_return {
      .breadcrumb_list_item {
        display: none;
      }
    }
    &_list {
      padding: 0 8px;
    }
  }
}

@media screen and (max-width: 450px) {
  .gl_breadcrumb {
    &_wrapper {
      &.list {
        justify-content: initial;
      }
    }
  }
}
