@mixin gl_pills() {
  margin-right: 40px;
  cursor: pointer;
  position: relative;
  display: inline-block;
  border-radius: 100%;
  color: $whiteColor;
  width: 64px;
  height: 64px;
  text-align: center;
  font-weight: $font-weight;
  transition: box-shadow 200ms;
  @include box-shadow-initial();
}

@mixin remove-animation() {
  transition: none;
  transform: none;
}

@mixin box-shadow-initial() {
  box-shadow: 0 0 0 0 rgba($whiteColor, 0);
}

@mixin gl_profil() {
  cursor: pointer;
  position: relative;
  border-radius: 100%;
  width: 214px;
  height: 214px;
  overflow: hidden;
  box-shadow: 0 0 30px 0 rgba($blackColor, 0.1);
}
