.gl_radioBtn, .gl_checkboxBtn {
  fieldset {
    border: none;
    display: flex;
    flex-direction: column;
    margin: 20px;
    align-items: flex-start;
  }
}

.gl_radio,
.gl_checkbox {
  display: flex;
  flex-direction: row;
  align-items: center;
  &.aside {
    color: $pinkish-gray;
    input {
      opacity: 0.3;
      &:checked:before {
        border-color: $blueberryColor;
        transition: 0.5s border-color ease;
        background-color: $whiteColor;
      }
    }
  }
  input {
    position: relative;
    appearance: none;
    margin: $margin-small;
    box-sizing: content-box;
    overflow: hidden;
    &:disabled:before {
      border-color: $pinkish-gray;
      background-color: $pinkish-gray;
    }
    &:before {
      content: '';
      display: block;
      box-sizing: content-box;
      width: $checkbox-size;
      height: $checkbox-size;
      border: 2px solid $pinkish-gray;
      transition: 0.2s border-color ease;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      box-sizing: content-box;
      top: 50%;
      left: 50%;
      transform-origin: 50% 50%;
      background-color: $blueberryColor;
      width: $checkbox-size;
      height: $checkbox-size;
      border-radius: 100vh;
      transform: translate(-50%, -50%) scale(0);
    }
  }
}
