.gl_btn {
  @include gl_btn();
  &:focus {
    @include focus();
  }
  &-icon {
    padding-right: $padding;
    padding-left: $padding;
  }
  &.disabled,
  &[disabled] {
    cursor: not-allowed;
  }

  // primary
  &-primary {
    @include box-shadow-initial();
    color: $whiteColor;
    background-color: $bittersweet;
    border-color: $bittersweet;
    transition: transform 200ms, box-shadow 200ms;
    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 4px 20px 0 rgba(253, 122, 105, 0.5);
    }
    &:active {
      @include remove-animation();
      @include box-shadow-initial();
    }
    &.disabled,
    &[disabled] {
      box-shadow: none;
      @include remove-animation();
      color: $whiteColor;
      background-color: $lightGray2;
      border-color: $lightGray2;
    }
  }

  // secondary
  &-secondary {
    color: $blueberryColor;
    background-color: $whiteColor;
    border-color: $blueberryColor;
    &.disabled,
    &[disabled] {
      color: $lightGray3;
      background-color: $whiteColor;
      border-color: $lightGray3;
    }
  }

  &-box {
    @include gl_mixin_boxBorder($border-radius-box);
    @include gl_mixin_secondBorder($border-radius-box, $blueberryColor);
    @include gl_mixin_boxcheck($blueberryColor, $whiteColor);
    position: relative;

    &.disabled,
    &[disabled] {
      color: $grayDarker;
      cursor: not-allowed;
    }

    &.animPulse {
      &::before {
        opacity: 1;
        transition: opacity 300ms ease;
      }
      &::after {
        animation-name: gl_anim_pulse;
        animation-duration: 300ms;
        animation-timing-function: ease;
        opacity: 1;
        transform: scale(.75);
      }
    }
  }

  // utility
  &-utility {
    padding: 16px 24px;
    color: $blueberryColor;
    background-color: $whiteColor;
    border-color: $lightGray3;
    font-weight: $font-weight;
    display: inline-flex;
    align-items: center;
    .gl-i-edit {
      font-size: 21px;
      padding: 8px 0 8px 8px;
    }
    &.second {
     color: $lightGray3;
      margin-top: $margin;
       .utilityAction {
         text-transform: uppercase;
         font-weight: normal;
         font-size: 14px;
       }
     }
    &.gl_btn-box {
      display: inline-flex;
      padding: 12px 24px;
      margin-right: $margin-md;
    }
  }
}
