.gl_tab {
  position: relative;
  font-family:"Nunito Sans",sans-serif;

  &_menu {
    margin: -8px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  &_item {
    margin: 8px;
  }

  &_link {
    padding: 15px;
    min-width: 112px;
    display: inline-block;
    border: 1px solid $lightGray3;
    border-radius: 4px;
    text-decoration: none;
    text-align: center;
    color: $lightGray3;
    font-size: 13px;
    cursor: pointer;
    transition: color 200ms, border-color 200ms;

    &.active, &:hover, &:active {
      border-color: $blueberryColor;
      color: $blueberryColor;
      text-decoration: none;
    }
  }

  &_content {
    margin-top: 16px;
  }

  &_panel {
    padding: 32px;
    display: none;

    &.active {
      display: block;
    }
  }
}